import 'buffer';
import 'stream-browserify';
import React, { useState } from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import SearchBar from './components/SearchBar';
import { xml2js } from 'xml-js'; // Import the xml2js function for XML parsing

function App() {
  const [position] = useState([48.8566, 2.3522]);
  const [searchResults, setSearchResults] = useState([]);

  const handleSearch = async (query) => {
    try {
      const response = await fetch(`/api/adresses?q=${query}`);
      const responseData = await response.text(); // Get the raw text instead of JSON object
      const parsedData = xml2js(responseData, { compact: true, ignoreComment: true });
      setSearchResults(parsedData.adresses.adresse || []); // Check if parsedData.adresses exists or not
    } catch (error) {
      console.error('Error searching addresses:', error);
    }
  };

  return (
    <div className="App">
      <h1>Contrôlez et fiabilisez les adresses</h1>
      <SearchBar onSearch={handleSearch} />

      <MapContainer center={position} zoom={13} style={{ height: '500px', width: '100%' }}>
        <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
        {searchResults.map((address, index) => (
          <Marker key={index} position={[address.geolocalisation[0], address.geolocalisation[1]]}>
            <Popup>{address.ligne}</Popup> {/* Use "ligne" instead of "adresse" for XML format */}
          </Marker>
        ))}
      </MapContainer>
    </div>
  );
}

export default App;
